import $ from '../core';

$.prototype.getAttr = function(attrName) {
  if (!attrName) {
    return this;
  }
  let arr = [];
  for (let i = 0; i < this.length; i += 1) {
    arr.push(this[i].getAttribute(attrName));
  }
  return arr;
};

$.prototype.setAttr = function(attrName, value) {
  if (!attrName || !value) {
    return this;
  }
  for (let i = 0; i < this.length; i += 1) {
    this[i].setAttribute(attrName, value);
  }
  return this;
};

$.prototype.toggleAttr = function(attrName, value) {
  if (!attrName) {
    return this;
  }
  for (let i = 0; i < this.length; i += 1) {
    if (this[i].hasAttribute(attrName)) {
      this[i].removeAttribute(attrName);
    } else {
      this[i].setAttribute(attrName, value);
    }
  }
  return this;
};

$.prototype.removeAttr = function(attrName) {
  if (!attrName) {
    return this;
  }
  for (let i = 0; i < this.length; i += 1) {
    if (this[i].hasAttribute(attrName)) {
      this[i].removeAttribute(attrName);
    }
  }
  return this;
};